  },
  computed: {
    ...mapGetters(['BaseUrl', 'CustInfo']),
    IAACheck(){
      let hasVal = false
      for(let index in this.custIaa){
        if(this.custIaa[index] !== ''){
          hasVal = true
        }
      }
      return hasVal
    },
  },
  watch: {
    "$route": 'initCustomerDetail1',
    CustInfo: function(newValue, oldValue) {
      if(oldValue.ID == '' && newValue.ID != '' && oldValue.serNo != '' && newValue.serNo != '') {
        let custInfo = newValue
        custInfo['serNo'] = ''
        this.updateCustInfo(custInfo)
        this.initCustomerDetail1();
      }
    }
  },
