      if (btn === '差額升等VIP') {
        this.panelOpen('vipModalOpen')
      }
    },
    financialSubmit(form) {
      this.panelClose('financialOpen')
      let updateData = Object.assign({}, form)
      let defaultFinanceTool = ['0','0','0','0','0','0','0','0','0']
      form.finance_tool.map(item => defaultFinanceTool[Number(item) -1] = item)
      updateData.finance_tool = defaultFinanceTool.join('')
      editCustFinancial(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, updateData).then(result => {
        if(result.header && result.header.status == '200') {
          this.$toast.info({title: '「客戶資料」已更新'})
          this.initCustomerDetail1()
        } else {
          this.$toast.info({title: '「客戶資料」更新失敗'})
        }
      })
      
    },
    dataSubmit(form) {
      this.panelClose('socialDataOpen')
      editCustInfo(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, null, form).then(result => {
        if(result.header.status && result.header.status == '200') {
          this.$toast.info({title: '「客戶資料」已更新'})
          this.initCustomerDetail1()
        } else {
          this.$toast.info({title: `「客戶資料」更新失敗 代碼:${result.header.status | ''}`})
        }
      })
    },
    selectGroup(item) {
      this.getGroupMamberByGrpId(this.$store.state.userInfo.id, item.grp_id, item)
    },
    reGetCustDetail(params) {
      this.panelClose('groupModalOpen')
      this.updateCustID(params.csid)
      this.loading_1 = true
      this.initCustomerDetail1()
    },
    submitIAAReport () {
      this.panelClose('iaaReportOpen')
      queryIAACust(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, 'queryAPI').then(result =>{
        if(result.body) {
          this.customerForIAA = result.body.detail || {}
        } 
      })
      this.panelOpen('iaaCheckOpen')
    },
    iaaCheckSubmit (isSuccess) {
      this.isReported = isSuccess
      this.panelClose('iaaCheckOpen')
      this.$toast.info({'title':'感謝您的回報，後續將由相關單位與客戶確認，並依實際聯繫情形更新資訊！！'})
    },
    iaaReportColse (isSuccess) {
      if(isSuccess) {
        this.isReported = isSuccess
        this.$toast.info({'title':'感謝您的回報，後續將由相關單位與客戶確認，並依實際聯繫情形更新資訊！！'})
      }
      this.panelClose('iaaReportOpen')
    },
    iaaCheckColse (isSuccess) {
      if(isSuccess) {
        this.isReported = isSuccess
        this.$toast.info({'title':'感謝您的回報，後續將由相關單位與客戶確認，並依實際聯繫情形更新資訊！！'})
      }
      this.panelClose('iaaCheckOpen')
    },
    setCustInfo(user) {
      let custInfo = {
        lv: Number(user.vip_lvl || '0'),
        familyLeader: false,
        name: user.name || '',
        gender: user.sex || 0,
        birthday: user.birthday || '',
        ID: user.id || '',
        age: user.age || '',
        serNo: user.ser_no || '',
        lineid: user.lineid || '',
        mobile: user.mobile? user.mobile.trim() : '',
        phone: [user.home_tel_area? user.home_tel_area.trim() : '', user.home_tel? user.home_tel.trim() : ''],
        office_phone: [user.corp_tel_area? user.corp_tel_area.trim() : '', user.corp_tel? user.corp_tel.trim() : '', user.corp_tel_ext? user.corp_tel_ext.trim() : ''],
        fax: [user.fax_area? user.fax_area.trim() : "", user.fax? user.fax.trim() : ""],
        // fax: '',
        address: [user.home_addr_zip? user.home_addr_zip.trim() : '', user.home_addr1? user.home_addr1.trim() : '', user.home_addr2? user.home_addr2.trim() : '', user.home_addr3? user.home_addr3.trim() : ''],
        office_address: [user.corp_addr_zip? user.corp_addr_zip.trim() :'', user.corp_addr1? user.corp_addr1.trim() :'', user.corp_addr2? user.corp_addr2.trim() :'', user.corp_addr3? user.corp_addr3.trim() :''],
        wg_name: user.wg_name || '',
      }
      this.updateCustInfo(custInfo)
      this.loading_1 = false
    },
    setCustPersonalInfo(user) {
      let idmark = ''

      if(user.idmark){
        let mark1 = user.idmark.source1 && user.idmark.source1 == '1'? '要': ''
        let mark2 = user.idmark.source2 && user.idmark.source2 == '1'? '被': ''
        let mark3 = user.idmark.source3 && user.idmark.source3 == '1'? '意': ''
        let mark4 = user.idmark.source4 && user.idmark.source4 == '1'? '產': ''
        let mark5 = user.idmark.source5 && user.idmark.source5 == '1'? '團': ''
        let mark6 = user.idmark.source6 && user.idmark.source6 == '1'? '借': ''
        idmark = mark1 + mark2 + mark3 + mark4 + mark5 + mark6
      }

      let relate1_pltn = user.relation ? user.relation.source1_pltn : '0'
      switch (relate1_pltn) {
        case '0':
          relate1_pltn = ''
          break
        case '1':
          relate1_pltn = '經手件'
          break
        case '2':
          relate1_pltn = '收費件'
          break
        case '3':
          relate1_pltn = '經手收費件'
          break
        default:
          relate1_pltn = ''
          break
      }

      let relation = ''
      if(user.relation){
        let relate1 = user.relation.source1 && user.relation.source1 == '1'? '壽': ''
        let relate2 = user.relation.source2 && user.relation.source2 == '1'? '產': ''
        let relate3 = user.relation.source3 && user.relation.source3 == '1'? '意': ''
        let relate4 = user.relation.source4 && user.relation.source4 == '1'? '借': ''
        let relate5 = user.relation.source5 && user.relation.source5 == '1'? '團': ''
        let relate6 = user.relation.source6 && user.relation.source6 == '1'? '他': ''
        relation = relate1 + relate2 + relate3 + relate4 + relate5 + relate6
      }

      this.relationStatus = user.relation

      // 轉換婚姻格式
      let transformMarry = ""
      switch (user.marry) {
        case "1" :
          transformMarry = "未婚";
          break;
        case "2" :
          transformMarry = "已婚";
          break;
        case "3" :
          transformMarry = "離婚";
          break;
        case "4" :
          transformMarry = "其他";
      }

      // 轉換學歷格式
      let transformEdu = ""
      switch (user.edu) {
        case "1" :
          transformEdu = "國中及以下";
          break;
        case "2" :
          transformEdu = "高中/職";
          break;
        case "3" :
          transformEdu = "專科";
          break;
        case "4" :
          transformEdu = "大學";
          break;
        case "5" :
          transformEdu = "研究所以上";
      }

      // 轉換職業格式
      let transformJob = ""
      switch (user.job) {
        case "1" :
          transformJob = "農業";
          break;
        case "2" :
          transformJob = "林業";
          break;
        case "3" :
          transformJob = "漁業";
          break;
        case "4" :
          transformJob = "畜牧業";
          break;
        case "5" :
          transformJob = "建築營造業";
          break;
        case "6" :
          transformJob = "製造業";
          break;
        case "7" :
          transformJob = "貿易業";
          break;
        case "8" :
          transformJob = "運輸倉儲業";
          break;
        case "9" :
          transformJob = "金融保險業";
          break;
        case "10" :
          transformJob = "娛樂運動業";
          break;
        case "11" :
          transformJob = "服務業";
          break;
        case "12" :
          transformJob = "餐旅美容業";
          break;
        case "13" :
          transformJob = "公務員";
          break;
        case "14" :
          transformJob = "教育業";
          break;
        case "15" :
          transformJob = "軍警業";
          break;
        case "16" :
          transformJob = "自由業";
          break;
        case "17" :
          transformJob = "家管";
          break;
        case "18" :
          transformJob = "學生";
          break;
        case "19" :
          transformJob = "無業";
          break;
        case "20" :
          transformJob = "中小學以上教師";
          break;
        case "21" :
          transformJob = "律師、醫師、會計師、建築師、引水人";
          break;
        case "22" :
          transformJob = "電子業";
          break;
        case "23" :
          transformJob = "資訊業";
          break;
        case "24" :
          transformJob = "其他";
      }
      
      this.custPersonalInfo = {
        remark: idmark.length > 0? idmark : '空',
        fax: [user.fax_area? user.fax_area.trim() : '', user.fax? user.fax.trim() : ''],
        career: transformJob,
        agency: user.corp || '',
        marriage: transformMarry,
        relatepltn : relate1_pltn,
        relate: relation.length > 0? relation: '空',
        introducer: user.introducer_name || '',
        introducerId: user.introducer || '',
        email: user.email || '',
        education: transformEdu
      }
    },
    isEmptyObj(obj){
      for(let key in obj){
        return false
      }
      return true
    },
    setCustIaa(iaa) {
      if(iaa.detail) {
        let iaaData = iaa.detail || {} 
        this.custIaa = {
          mobile1: iaaData.mobile01 || '',
          mobile2: iaaData.mobile02 || '',
          phone: iaaData.tel_newest_area || iaaData.tel_newest ? iaaData.tel_newest_area +'-'+ iaaData.tel_newest : '',
          address: iaaData.addr_newest_postal_cd + iaaData.addr_newest_city_nm + iaaData.addr_newest_line_1 + iaaData.addr_newest_line_2,
          email: iaaData.email || ''
        }
      }
      
    },
    setCustFinancial(finanical) {
      
      if(this.isEmptyObj(finanical)){
        return false
      }

      // 轉換家庭年收入格式
      let transformFamily_incom = ""
      switch (finanical.family_incom) {
        case "1" :
          transformFamily_incom = "60萬以下";
          break;
        case "2" :
          transformFamily_incom = "61萬~80萬";
          break;
        case "3" :
          transformFamily_incom = "81萬~100萬";
          break;
        case "4" :
          transformFamily_incom = "101萬~150萬";
          break;
        case "5" :
          transformFamily_incom = "151萬~200萬";
          break;
        case "6" :
          transformFamily_incom = "201萬~350萬";
          break;
        case "7" :
          transformFamily_incom = "351萬以上";
      }

      // 轉換個人年所得格式
      let transformIncommy = ""
      switch (finanical.incommy) {
        case "1" :
          transformIncommy = "無";
          break;
        case "2" :
          transformIncommy = "30萬以下";
          break;
        case "3" :
          transformIncommy = "31~60萬";
          break;
        case "4" :
          transformIncommy = "61~100萬";
          break;
        case "5" :
          transformIncommy = "101~200萬";
          break;
        case "6" :
          transformIncommy = "201萬以上";
      }

      // 轉換月平均投資格式
      let transformInvest = ""
      switch (finanical.invest) {
        case "1" :
          transformInvest = "無";
          break;
        case "2" :
          transformInvest = "3千以下";
          break;
        case "3" :
          transformInvest = "3千~5千";
          break;
        case "4" :
          transformInvest = "5千~1萬";
          break;
        case "5" :
          transformInvest = "1萬~2萬";
          break;
        case "6" :
          transformInvest = "2萬~5萬";
          break;
        case "7" :
          transformInvest = "5萬以上";
      }
      
      // 轉換住屋狀況格式
      let transformHouse = ""
      switch (finanical.house) {
        case "1" :
          transformHouse = "自有";
          break;
        case "2" :
          transformHouse = "租賃";
          break;
        case "3" :
          transformHouse = "其他親屬所有";
          break;
        case "4" :
          transformHouse = "其他";
      }

      // 轉換交通工具格式
      let transformCar = ""
      switch (finanical.car) {
        case "1" :
          transformCar = "無";
          break;
        case "2" :
          transformCar = "機車";
          break;
        case "3" :
          transformCar = "汽車";
          break;
        case "4" :
          transformCar = "機車與汽車";
      }

      // 轉換理財工具格式
      let tools = [
        {text:'定存', val: '1'},
        {text:'保險', val: '2'},
        {text:'共同基金', val: '3'},
        {text:'標會', val: '4'},
        {text:'債券', val: '5'},
        {text:'股票', val: '6'},
        {text:'期貨外匯', val: '7'},
        {text:'不動產', val: '8'},
        {text:'其他', val: '9'}
      ]
      let transformtools = []
      let filtertools = tools.filter(item => finanical.finance.indexOf(item.val) >= 0).map(item => transformtools.push(item.text))
      transformtools = transformtools.join()


      this.custFinancial = {
        household_income: transformFamily_incom|| '',
        personal_income: transformIncommy|| '',
        monthly_invest: transformInvest || '',  //0
        finance_tool: transformtools || '',
        houseloan: finanical.loan_amt || '',  //0
        housing: transformHouse|| '',
        vehicle: transformCar|| ''
      }
    },
    setCustDecline(refuse_mark, comstomer) {
      this.custDecline = [
        refuse_mark.sms_promote === '1' ? 1:0,
        refuse_mark.phone_promote === '1' ? 2:0,
        refuse_mark.email_promote === '1' ? 3:0,
        refuse_mark.mail_promote === '1' ? 4:0,
        refuse_mark.agnt_promote === '1' ? 5:0,
        refuse_mark.stop_personal_data === '1' ? 6 :0,
        comstomer.relation && (comstomer.relation.source2 === '1' || comstomer.relation.source2 === '2')? 7:0
      ]     
    },
    setCustServiceItem(serviceCheck) {
      if(serviceCheck.changeonline === 'Y') {
        this.changeonline.status = 1
      }
      if(serviceCheck.eserver && serviceCheck.eserver.indexOf('已辦理') > -1 ) {
        this.serviceESERVER.status = 1
        this.serviceESERVER.link = 1
        this.serviceESERVER.positiveMsg = serviceCheck.eserver
      }
      if(serviceCheck.payment_apply && serviceCheck.payment_apply.length > 0) {
        this.servicePAYMENT_APPLY.status = 1
        this.servicePAYMENT_APPLY.link = 1
        this.servicePAYMENT_APPLY.payment_apply = serviceCheck.payment_apply
      }
      if(serviceCheck.csemail && serviceCheck.csemail.indexOf('未辦理') < 0 && serviceCheck.csemail.length > 0) {
        this.serviceCSEMAIL.status = 1
        this.serviceCSEMAIL.link = 1
        this.serviceCSEMAIL.csemail = serviceCheck.csemail
      }
      if(serviceCheck.plcnos && serviceCheck.plcnos.length > 0) {
        this.servicePLCNOS.status = 1
        this.servicePLCNOS.link = 1
        this.servicePLCNOS.plcnos = serviceCheck.plcnos
      }
      if(serviceCheck.show_ecall && serviceCheck.show_ecall.indexOf('已辦理') > -1) {
        this.serviceSHOW_ECALL.status = 1
      } else if(serviceCheck.show_ecall && serviceCheck.show_ecall.indexOf('詳細資訊') > -1 ) {
        this.serviceSHOW_ECALL.status = 1
        this.serviceSHOW_ECALL.link = 1
        this.serviceSHOW_ECALL.positiveMsg = serviceCheck.show_ecall
        this.serviceSHOW_ECALL.ecall = serviceCheck.ecall
      }
      if(serviceCheck.pl_loan && serviceCheck.pl_loan.length > 0) {
        this.servicePL_LOAN.status = 1
        this.servicePL_LOAN.link = 1
        this.servicePL_LOAN.pl_loan = serviceCheck.pl_loan
      }
      if(serviceCheck.claim_offset_bill && serviceCheck.claim_offset_bill.indexOf('尚未申辦') < 0) {
        this.serviceCLAIM_OFFSET_BILL.status = 1
      }
    },
    checkIsOpenServiceModal(service) {
      if(service.link == 1) {
        this.selectedServiceInstance = service
        this.panelOpen('serviceModalOpen')
      }
    },
    getGroupMamberByGrpId(userId, grpid, item) {
      getGroupMember(userId, grpid).then(result => {
        this.grpMemList = result
        this.selectedGroup = item
        this.panelOpen('groupModalOpen')
      })
    },
    addToGrpSubmit(newSelectedGroups) {
      // let newGrpsIdSet = new Set(newSelectedGroups.map(item => item.grp_id))
      // let removeCustFromGroups = this.grpsOfCust.filter(item => !newGrpsIdSet.has(item.grp_id))

      // let oriGrpsOfCustSet = new Set(this.grpsOfCust.map(item => item.grp_id))
      // let addCustToGroups = newSelectedGroups.filter(item => !oriGrpsOfCustSet.has(item.grp_id))
      updateGroupsByCust(this.$store.state.userInfo.id, this.$store.state.userInfo.id, this.$store.state.custInfo.ID, newSelectedGroups).then(result => {
        if(result.header && result.header.status === '200') {
          getGroupOfCust(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result2 => {
            if(result2.body.groups) {
              this.grpsOfCust = result2.body.groups.map(item => {
                return {
                  grp_id: item.grp_id,
                  grp_name: item.grp_name
                }
              })
            } else {
              this.grpsOfCust = []
            }
          })

          if(result.header.status == '200') {
            this.$toast.info({title: '加入群組成功'})
          } else {
            //custInfo.name
            let message = result.header.message || ''
            message = message.replace('%', this.$store.state.custInfo.name)
            this.$toast.info({title: message})
          }
        } else {
          let message = result.header.message || ''
          message = message.replace('%', this.$store.state.custInfo.name)
          this.$toast.info({title: `${message}`})
        }
      })
      this.panelClose('addToGroupOpen')
    },
    openIAA(){
      this.panelOpen('iaaReportOpen')
       queryIAA(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, 'query').then(result =>{
         var check_item = result.body.check_item
         if(check_item) {
           this.chkIAAData = []
           
           this.isFound = true
           if(check_item.addr == 1) {
             this.chkIAAData.push('address')
           }
           if(check_item.email == 1) {
             this.chkIAAData.push('email')
           }
           if(check_item.mobile1 == 1) {
             this.chkIAAData.push('mobile1')
           }
           if(check_item.mobile2 == 1) {
             this.chkIAAData.push('mobile2')
           }
           if(check_item.tel == 1) {
             this.chkIAAData.push('phone')
           }
           
         } else {
           this.isFound = false
           this.chkIAAData = []
         }
         
      })
    },
    initCustomerDetail1() {
      // if(this.$route.params.id === 'origin' && localStorage.backPageID){ // for 介紹人上一頁  讀取 query
      //   this.updateCustID(localStorage.backPageID)
      // }
      let loadCustId = this.$route.params.id
      if(loadCustId === 'origin'){
        //什麼事都不做
      }else if(loadCustId !== 'SerNo'){
        this.updateCustID(localStorage[loadCustId])
      }else{
        this.updateSerNo(localStorage[loadCustId])
      }
      this.loading_1 = true
      getCustInfo(this.$store.state.userInfo.id, this.$store.state.userInfo.id, (this.$store.state.custInfo.ID || this.$store.state.custInfo.serNo)).then(result => {
        if(result.header && result.header.status === '200'){
          this.setCustInfo(result.body.customer)
          this.setCustPersonalInfo(result.body.customer)
          this.setCustFinancial(result.body.financial)
          this.setCustDecline(result.body.refuse_mark, result.body.customer)
          this.setCustServiceItem(result.body.service_check)
          this.loading_1 = false
        }else{
          this.loading_1 = false
          this.$toast.info({title: result.header.message})
        }

      })
      getCustIAA(this.$store.state.userInfo.id, this.$store.state.custInfo.ID, 'queryAPI').then(result => {
        this.loading_2 = true
        if(result.header && result.header.status === '200'){
          this.setCustIaa(result.body)
          if(result.body.check_item && result.body.check_item.upd_time) {
            let upd_time = result.body.check_item.upd_time

            if(result.body.detail) {
              let addr_newest_update = result.body.detail.addr_newest_update 
              let email_update = result.body.detail.email_update 
              let mobile01_update = result.body.detail.mobile01_update 
              let mobile02_update = result.body.detail.mobile02_update 
              let tel_newest_update = result.body.detail.tel_newest_update
              
              if(addr_newest_update > upd_time || 
                email_update > upd_time || 
                mobile01_update > upd_time ||
                mobile01_update > upd_time ||
                tel_newest_update > upd_time ) {
                  this.isReported = false
                } else {
                  this.isReported = true
                } 
              
            } else {
              this.isReported = true
            }
            
          }
          this.loading_2 = false
        }else{
          this.$toast.info({title: result.header.message})
          this.loading_2 = false
        }
      })
      getGroupOfCust(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result => {
        if(result.body.groups) {
          this.grpsOfCust = result.body.groups.map(item => {
            return {
              grp_id: item.grp_id,
              grp_name: item.grp_name
            }
          })
        } else {
          this.grpsOfCust = []
        }
      })
      getGroupOfAgnt(this.$store.state.userInfo.id, this.$store.state.userInfo.id).then(result => {
        const transformedDate = []
        if(result.body && result.body.groups){
          result.body.groups.forEach((grp) => {
            const data = {
              grp_id: grp.grp_id,
              grp_name: grp.grp_name,
              edit: false
            }
            transformedDate.push(data)
          })
          this.grpsOfAgnt = transformedDate
        }else{
          this.grpsOfAgnt = transformedDate
        }
      })

      // 客戶如果無房貸資料 disable 房貸資訊頁籤
      queryHousingLoans(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result => {
        if(result && result.body && result.body.length > 0){
          this.$store.dispatch('updaHousingLoans',true)
        }else{
          this.$store.dispatch('updaHousingLoans',false)
        }
      })

      getCustDie(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result => {
        if(result.header && result.header.status === '200'){
          this.custDie = true
        }else{
          this.custDie = false
        }
      })
    },
    routeToCustDetail(custId) {  
      let IntrAdd = ''
      if(this.$route.params.id.indexOf('intr') === -1){
        IntrAdd = 'intr1'
      }else{
        IntrAdd = 'intr' + (this.$route.params.id.replace('intr','') - 0 + 1)
      }
      localStorage[IntrAdd] = custId
      this.updateCustID(custId)
      this.$router.push({path: `/detail/${IntrAdd}/tab/1`})
    },
    getFax(val){
      if(val !== 'undefined'){
        if(val[0]){
        return val[0]+'-'+val[1];
      }else{
        return '';
      }
      }
    }
  },
  beforeMount () {
    let routeNumber = (this.$route.name.split(/-/g)).reverse()
    this.currentTab = Number(routeNumber[0])

    this.serviceItems = [
      this.serviceCHANGEONLINE,
      this.serviceESERVER,
      this.servicePAYMENT_APPLY,
      this.serviceCSEMAIL,
      this.servicePLCNOS,
      this.serviceSHOW_ECALL,
      this.servicePL_LOAN,
      this.serviceCLAIM_OFFSET_BILL
    ]
  },
  mounted () {
    this.initCustomerDetail1()
    // setTimeout(() => {
    //   // get first api and change loading section 1
    //   this.loading_1 = false;
    // }, 3000)
    // setTimeout(() => {
    //   // get second api and change loading section 2
    //   this.loading_2 = false;
    // }, 6000)
    // setTimeout(() => {
    //   // get api and change loading section 3
    //   this.loading_3 = false;
    // }, 9000)
  }
}
</script>
