<template>
  <div class="modal fade confirm" :class="{'is-open': open}">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h3 class="modal-title">編輯個人資訊</h3>
        </div>
        <div class="modal-body">
          <form class="form">
            <div class="form-group row">
              <label for="birth-year" class="col-3 text-right">Email</label>
              <div class="col-9">
                <input type="email" class="form-control form-control-inline" v-model="form.email">
              </div>
            </div>
            <div class="form-group row">
              <label for="gender-1" class="col-3 text-right">傳真</label>
              <div class="col-9">
                <input type="tel" @input="numberFax0" class="form-control form-control-inline form-short" v-model="form.fax[0]">
                <span class="text">-</span>
                <input type="tel" @input="numberFax1" class="form-control form-control-inline" v-model="form.fax[1]">
              </div>
            </div>
            <div class="form-group row">
              <label for="gender-1" class="col-3 text-right">職業</label>
              <div class="col-9">
                <select class="form-control form-control-inline form-large" v-model="form.career">
                  <option value="">請選擇</option>
                  <option
                  v-for="(c, cid) in careerOpt"
                  :key="`city-item-${cid}`"
                  :value="c.val">{{c.text}}</option>
                </select>
              </div>
            </div>
            <div class="form-group row">
              <label for="gender-1" class="col-3 text-right">介紹人</label>
              <div class="col-9">
                <span class="px-3">{{ form.introducer }}</span>
                <!-- <input type="text" class="form-control form-control-inline" v-model="form.introducer" disabled> -->
                <a class="btn btn-default m-0"
                @click="clientSearchOpen = true">
                  從既有客戶查詢
                </a>
              </div>
            </div>
            <div class="form-group row">
              <label for="gender-1" class="col-3 text-right">服務單位</label>
              <div class="col-9">
                <input type="text" class="form-control form-control-inline" v-model="form.agency">
              </div>
            </div>
            <div class="form-group row">
              <label for="gender-1" class="col-3 text-right">學歷</label>
              <div class="col-9">
                <select class="form-control form-control-inline form-wide" v-model="form.education">
                  <option value="">請選擇</option>
                  <option
                  v-for="(e, eid) in eduOpt"
                  :key="`city-item-${eid}`"
                  :value="e.val">{{e.text}}</option>
                </select>
              </div>
            </div>
            <div class="form-group row">
              <label for="gender-1" class="col-3 text-right">婚姻</label>
              <div class="col-9">
                <select class="form-control form-control-inline" v-model="form.marriage">
                  <option value="">請選擇</option>
                  <option
                  v-for="(m, mid) in marriageOpt"
                  :key="`city-item-${mid}`"
                  :value="m.val">{{m.text}}</option>
                </select>
              </div>
            </div>

            <div class="action text-right">
              <a @click="$emit('close')" class="btn btn-default">
                取消
              </a>
              <a @click.stop.prevent="submit()" class="btn btn-primary">
                儲存
              </a>
            </div>
          </form>
        </div>
      </div>
    </div>
    <search-client-modal
    :open="clientSearchOpen"
    @submit="submitClient"
    @close="clientSearchOpen = false" />
  </div>
</template>

<script>
import SearchClientModal from './SearchClientModal'

export default {
  name: 'SocailDataModal',
  props: {
    open: {
      type: Boolean,
      default: false
    },
    inputs: {
      type: Object,
      default: null
    }
  },
  watch: {
    'open': 'transformToEnum'
  },
  components: {SearchClientModal},
  data () {
    return {
      careerOpt: [
        {text:'農業', val: '1'},
        {text:'林業', val: '2'},
        {text:'漁業', val: '3'},
        {text:'畜牧業', val: '4'},
        {text:'建築營造業', val: '5'},
        {text:'製造業', val: '6'},
        {text:'貿易業', val: '7'},
        {text:'運輸倉儲業', val: '8'},
        {text:'金融保險業', val: '9'},
        {text:'娛樂運動業', val: '10'},
        {text:'服務業', val: '11'},
        {text:'餐旅美容業', val: '12'},
        {text:'公務員', val: '13'},
        {text:'教育業', val: '14'},
        {text:'軍警業', val: '15'},
        {text:'自由業', val: '16'},
        {text:'家管', val: '17'},
        {text:'學生', val: '18'},
        {text:'無業', val: '19'},
        {text:'中小學以上教師', val: '20'},
        {text:'律師、醫師、會計師、建築師、引水人', val: '21'},
        {text:'電子業', val: '22'},
        {text:'資訊業', val: '23'},
        {text:'其他', val: '24'}
      ],
      eduOpt: [
        {text:'國中及以下', val: '1'},
        {text:'高中/職', val: '2'},
        {text:'專科', val: '3'},
        {text:'大學', val: '4'},
        {text:'研究所以上', val: '5'}
      ],
      marriageOpt: [
        {text:'未婚', val: '1'},
        {text:'已婚', val: '2'},
        {text:'離婚', val: '3'},
        {text:'其他', val: '4'}
      ],
      clientSearchOpen: false,
      form: {
        fax: ['', ''],
        email: '',
        career: '',
        introducer: '',
        introducerId: '',
        agency: '',
        education: '',
        marriage: '',
        serNo: '',
      }
      // form: {
      //   lineid: !this.inputs ? 0 : this.inputs.lineid,
      //   email: !this.inputs ? 0 : this.inputs.email,
      //   career: !this.inputs ? 0 : this.inputs.career,
      //   introducer: !this.inputs ? 0 : this.inputs.introducer,
      //   agency: !this.inputs ? 0 : this.inputs.agency,
      //   education: !this.inputs ? 0 : this.inputs.education,
      //   marriage: !this.inputs ? 0 : this.inputs.marriage
      // }
    }
  },
  methods: {
    numberFax0(){
      this.form.fax[0] = this.form.fax[0].replace(/[^\d]/g,'');
    },
    numberFax1(){
      this.form.fax[1] = this.form.fax[1].replace(/[^\d]/g,'');
    },
    submitClient (member) {
      this.form.introducer = member.name
      this.form.introducerId = member.id
      this.clientSearchOpen = false
    },
    submit () {
      let emailPattern = /^[\w\-\.]+\@([\w]+\.)+/g
      if(emailPattern.test(this.form.email) || this.form.email === ''){
        if(this.$store.state.custInfo.ID === ''){
          this.form.serNo = this.$store.state.custInfo.serNo
        }
        this.$emit('submit', this.form)
        // this.$toast.info({title: 'Email 格式成功'})
      }else{
        this.$toast.info({title: 'Email 格式不對請重新輸入'})
      }
      
    },
    transformToEnum(val) {
      
      if(val && this.inputs) {
       
        this.form.email = this.inputs.email
        let uncheckCareer = this.careerOpt.filter(item => item.text == this.inputs.career)
        this.form.career = uncheckCareer.length ? uncheckCareer[0].val : ''
        this.form.introducer = this.inputs.introducer
        this.form.introducerId = this.inputs.introducerId
        this.form.agency = this.inputs.agency
        let uncheckEdu = this.eduOpt.filter(item => item.text == this.inputs.education)
        this.form.education = uncheckEdu.length? uncheckEdu[0].val : ''
        let uncheckMarriage = this.marriageOpt.filter(item => item.text == this.inputs.marriage)
        this.form.marriage = uncheckMarriage.length? uncheckMarriage[0].val : ''
        this.form.fax = this.inputs.fax
        
      }
    }
  },
  mounted () {
  }
}
</script>
